}


/*Global Variables
--n1:for width and height of logo in navbar
--n2:for width and height of bar in navbar
--n3:font-size of text in navbar
--nw: default width of navbar
*/

:root
{
   --n1:3.125rem; 
   --n2:1.5rem;
   --n3:1.2rem;
   --nw: 85vw;
}

/*Navbar*/
.navbar
{
    background: var(--Gr1);
    width: 85vw;
    width: var(--nw);
    padding:0 10px;
    margin-top:5vh;
    border-radius: 15px 15px 0px 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

/*logo*/
.logo img
{
    width:3.125rem;
    width:var(--n1);
    height:3.125rem;
    height:var(--n1);
    padding:5px 5px;
}
/*bars for hamburger menu*/
.bar
{
    display:none;
    width:1.5rem;
    width:var(--n2);
    height:1.5rem;
    height:var(--n2);
    position:absolute;
    right:10px;
    top:10px;
    fill: var(--r2);
    cursor:pointer;
}

/*Navbar-content*/
.navbar ul
{
    list-style: none;
    display: flex;
    flex-direction: row;
}

.navbar ul li 
{
    display: flex;
    align-content: center;
    justify-content: center;
    margin:0px 10px;
}
.navbar ul li .link
{
    text-decoration: none;
    font-family: var(--f1);
    font-weight: 300;
    padding:5px;
    color: var(--r1);
    font-size:1.2rem;
    font-size:var(--n3);
}

.navbar ul li .link:hover
{
    background:var(--Gr2);
    font-weight: bolder;
    border-radius: 10px;
}

/*Highlighting active page*/
.activePage
{
    background: var(--Gr2);
    border-radius: 10px;
}

/*Media queries*/

/*Max-width media Queries*/

/*Media Queries for width<=760*/
@media only screen and (max-width:760px)
{
    :root
    {
        --nw:95vw;
    }

    .navbar
    {
        margin-top:1vh; 
    }
}

/*Media Queries for width<=680*/
@media only screen and (max-width:680px)
{
    .bar
    {
        display: inline;
        display: initial;
    }
    :root
    {
        --nw:100vw;
    }
    .navbar
    {
        margin-top:0vh;   
        border-radius: 0px;
        flex-direction: column;
        align-items:flex-start;
    }
    .navbar ul
    {
        display: grid;
        grid-template-columns: repeat(1,95% );
        grid-row-gap: 5px;
        row-gap: 5px;
        justify-content: center;
        width:100vw;
    }
    .navbar ul li
    {
        justify-content: left;
        margin: 0px;
    }
    .navbar ul li .link
    {
        width:100%;
    }
    .navbar ul li .link:hover
    {
        background:transparent;
        font-weight: bolder;
    }
    .activePage
    {
        background:transparent;
        /* color: rgb(0, 50, 50)!important; */
        font-weight: bolder!important;
    }
    .visibility
    {
       display: none!important;
    }
}

