/*Slider*/
.slider
{
    width:150px;
    width:var(--a6);
    height:150px;
    height:var(--a6);
    display: grid;
    grid-template-columns: repeat(17,1fr );
    grid-template-columns: repeat(var(--an),1fr );
    margin-left:10px;
    overflow-x: hidden;
    overflow-y: hidden;
}
.slider img
{
    width: 150px;
    width: var(--a6);
    height:150px;
    height:var(--a6);
    position: relative;
    -webkit-animation-name:slide_animation;
            animation-name:slide_animation;
    -webkit-animation-duration:30s;
            animation-duration:30s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-direction:alternate;
            animation-direction:alternate;
    -webkit-animation-play-state:running;
            animation-play-state:running
}
/*slide_animation*/

@-webkit-keyframes slide_animation
{
    0%{right:calc((150px)*0);right:calc((var(--a6))*0);}
    6.25%{right:calc(150px*1);right:calc(var(--a6)*1);}
    12.50%{right:calc(150px*2);right:calc(var(--a6)*2);}
    18.75%{right:calc(150px*3);right:calc(var(--a6)*3);}
    25.00%{right:calc(150px*4);right:calc(var(--a6)*4);}
    31.25%{right:calc(150px*5);right:calc(var(--a6)*5);}
    37.50%{right:calc(150px*6);right:calc(var(--a6)*6);}
    43.75%{right:calc(150px*7);right:calc(var(--a6)*7);}
    50.00%{right:calc(150px*8);right:calc(var(--a6)*8);}
    56.25%{right:calc(150px*9);right:calc(var(--a6)*9);}
    62.50%{right:calc(150px*10);right:calc(var(--a6)*10);}
    68.75%{right:calc(150px*11);right:calc(var(--a6)*11);}
    75.00%{right:calc(150px*12);right:calc(var(--a6)*12);}
    81.25%{right:calc(150px*13);right:calc(var(--a6)*13);}
    87.50%{right:calc(150px*14);right:calc(var(--a6)*14);}
    93.75%{right:calc(150px*15);right:calc(var(--a6)*15);}
    100.00%{right:calc(150px*16);right:calc(var(--a6)*16);}
}

@keyframes slide_animation
{
    0%{right:calc((150px)*0);right:calc((var(--a6))*0);}
    6.25%{right:calc(150px*1);right:calc(var(--a6)*1);}
    12.50%{right:calc(150px*2);right:calc(var(--a6)*2);}
    18.75%{right:calc(150px*3);right:calc(var(--a6)*3);}
    25.00%{right:calc(150px*4);right:calc(var(--a6)*4);}
    31.25%{right:calc(150px*5);right:calc(var(--a6)*5);}
    37.50%{right:calc(150px*6);right:calc(var(--a6)*6);}
    43.75%{right:calc(150px*7);right:calc(var(--a6)*7);}
    50.00%{right:calc(150px*8);right:calc(var(--a6)*8);}
    56.25%{right:calc(150px*9);right:calc(var(--a6)*9);}
    62.50%{right:calc(150px*10);right:calc(var(--a6)*10);}
    68.75%{right:calc(150px*11);right:calc(var(--a6)*11);}
    75.00%{right:calc(150px*12);right:calc(var(--a6)*12);}
    81.25%{right:calc(150px*13);right:calc(var(--a6)*13);}
    87.50%{right:calc(150px*14);right:calc(var(--a6)*14);}
