/*Max-width media Queries*/

/*Media queries for <=1100px width*/
@media only screen and (max-width:1100px)
{
    /*setting post direction to column*/
    .post
    {
        display: flex;
        flex-direction: column;
    }
    .postInfo
    {
        align-items: start;
        padding:10px 5px 20px 10px;
