/*Global-Variables comman to all posts
--pstw:default width of  post page
--pstmwp:default width of post within post page
--pst1:font size of main heading of the blog
--pst2:font size of sub heading of the blog
--pst3:font size of all paras in the blog and H3-headings(sub-sub-headings)
--pst4:width and height of the download pdf icon and font size of download attribute
*/

:root
{
    --pstw:85vw;
    --pstmwp:95%;
    --pst1:2rem;
    --pst2:1.5rem;
    --pst3:1.2rem;
    --pst4:1.2rem;
}


/*Main-post*/
.main
{
    width:85vw;
    width:var(--pstw);
    padding:10px;
    background: var(--b3);
    display: flex;
    flex-direction: column;
    align-items: center;
    
}
.main_post
{
    margin:30px 0px;
    width: 95%;
    width: var(--pstmwp);
    display: flex;
    flex-direction: column;
    background: var(--b6);
}

/*Post-desc*/
.main_post  h1
{
    font-family: var(--f2);
    color: var(--g3);
    font-size: 2rem;
    font-size: var(--pst1);
    padding-left:10px;
}
/*Featured_img*/
.featured_image
{
    margin: 20px 0px;
    padding:0px 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
img
{
    width:100%;
}
video
{
    width: 100%;
}
/*Coloring the tips*/
.scarlet_red
{
    color: var(--r1);
    font-weight: bold;
}

